.main-div {
  .home-banner {
    background-image: url("../Assets/home-banner.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 695px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;

    .header-texts {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      margin-left: 30px;
      .help-text {
        width: 222px;
        height: 17px;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 0.1em;
        text-transform: uppercase;

        color: #0de3d6;
      }
      .launch-text {
        width: 650px;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 600;
        font-size: 48px;
        line-height: 120%;
        /* or 58px */

        color: #ffffff;
      }
      .launch-sub-text {
        width: 500px;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 26px;
        /* or 180% */

        letter-spacing: 0.02em;

        color: #ffffff;

        opacity: 0.9;
      }
      .apply-section {
        display: flex;
        flex-direction: row;
        align-items: center;

        .apply-now-btn {
          display: flex;
          flex-direction: row;
          align-items: center;
          padding: 12px 36px;
          margin-right: 20px;
          gap: 10px;
          font-family: "Roboto";
          font-style: normal;
          font-weight: 800;
          font-size: 16px;
          line-height: 19px;

          color: black;
          background: #ffffff;
        }
        .hire-students {
          font-style: normal;
          font-weight: 800;
          font-size: 16px;
          line-height: 19px;
          /* identical to box height */
          margin-left: 10px;
          letter-spacing: 2px;
          text-decoration-line: underline;
          font-family: "Roboto";
          color: #ffffff;
        }
      }
    }
    .header-images {
      display: flex;
      justify-content: center;
      align-items: center;
      .guy-with-laptop {
        width: 550px;
        height: 514px;
      }
    }
  }
  .who-we-serve-section {
    display: flex;
    flex-direction: column;
    margin-top: 80px;
    .header-section {
      display: flex;
      flex-direction: column;
      align-items: center;
      .heading {
        width: 952px;
        height: 45px;

        font-family: "Roboto";
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 45px;
        /* identical to box height */
        text-align: center;
        letter-spacing: 4px;
        text-transform: capitalize;

        color: #212121;
      }

      .sub-heading {
        width: 378px;
        height: 23px;

        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 23px;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.2px;
        text-transform: capitalize;

        /* systemGrey/600 */

        color: #757575;
      }
    }
    .options-section {
      display: flex;
      flex-direction: row;
      padding: 0px;
      gap: 6px;
      align-items: center;
      justify-content: center;
      margin-top: 80px;
      height: 40px;

      .option {
        padding: 22px 45px;
        gap: 10px;

        width: 162px;
        height: 40px;

        background: #f2f2f2;
        border-radius: 38px;

        /* Inside auto layout */
        font-family: "Roboto";
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 16px;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.05em;
        text-transform: uppercase;

        color: #646464;

        flex: none;
        order: 1;
        flex-grow: 0;
        &.selected {
          background: #4285f3;
          color: #ffffff;
        }
      }
    }
    .learn-more-section {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .learn-more-btn {
        margin-top: 20px;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        padding: 12px 36px;
        justify-content: center;
        align-items: center;
        height: 57px;

        font-style: normal;
        font-weight: 800;
        font-size: 18px;
        line-height: 33px;
        /* identical to box height, or 183% */

        letter-spacing: 2px;

        color: #ffffff;

        /* Inside auto layout */

        flex: none;
        order: 0;
        flex-grow: 0;
        background: linear-gradient(0deg, #fac30f, #fac30f),
          linear-gradient(0deg, #fac30f, #fac30f), #fac30f;
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2);
        //border-radius: 28px;
      }
    }
  }
  .candidate-review-section {
    margin-top: 50px;
    background: #4285f4;
    display: flex;
    flex-direction: column;
    height: 545px;
    padding-bottom: 32px;
    padding-top: 32px;
    .header-wrapper {
      justify-content: center;
      align-items: center;
      display: flex;
      .heading {
        font-family: "Libre Baskerville";
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 45px;
        /* identical to box height */

        display: flex;
        align-items: center;
        text-align: center;

        text-transform: capitalize;

        color: #ffffff;
        margin: 1.5rem;
      }
    }

    .review-wrapper {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      height: max-content;
      scroll-behavior: smooth;
      margin-top: 50px;

      .wrapper {
        display: flex;
        flex-direction: column;
        .user-image {
          position: absolute;
          top: -6%;
        }
        .card {
          box-sizing: border-box;
          margin-left: 20px;
          left: 0%;
          right: 0%;
          top: 12.79%;
          bottom: 0%;
          height: 300px;
          min-width: 400px;
          background: #ffffff;
          border: 1px solid #d8d8d8;
          border-radius: 12px;
          display: flex;
          flex-direction: column;
          justify-content: space-evenly;
          margin-top: 60px;
          .review {
            font-family: "Roboto";
            font-style: normal;
            font-weight: 700;
            font-size: 16px;
            line-height: 180%;
            /* or 29px */

            letter-spacing: 0.15px;

            color: #314150;
            margin: 2rem;
          }
          .line {
            margin: 1rem;
            width: 90%;
            height: 47px;
            border-bottom: 1px solid #f2f2f2;
          }
          .user-info {
          }
          .username {
            margin-top: 50px;
            left: 6.15%;
            right: 61.54%;
            top: 73.11%;
            bottom: 20.66%;

            font-family: "Roboto";
            font-style: normal;
            font-weight: 700;
            font-size: 16px;
            line-height: 19px;
            /* identical to box height */

            color: #314150;
            margin: 2rem;
          }
          .designation {
            margin-top: 20px;
            left: 6.15%;
            right: 64.87%;
            top: 82.62%;
            bottom: 11.15%;

            font-family: "Roboto";
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            /* identical to box height */

            color: #646464;
            margin: 2rem;
          }
        }
      }
    }
  }
  .bottom-section {
    height: 1400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #dfe7f6;
    .frequently-asked-section {
      display: flex;
      flex-direction: column;
      align-items: center;
      .heading {
        width: 770px;
        height: 80px;
        left: 298px;
        top: 2796px;

        font-family: "Libre Baskerville";
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 80px;
        /* identical to box height, or 222% */

        text-align: center;
        letter-spacing: -0.04em;

        color: #111111;
        // margin: 1rem;
      }
      .sub-heading {
        width: 964px;
        height: 68px;
        left: 201px;
        top: 2874px;

        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 170%;
        /* or 34px */

        text-align: center;

        color: #757575;
      }
      .accordion-wrapper {
        .accordion {
          display: flex;
          flex-direction: column;
          .item {
            height: max-content;
            left: 0%;
            right: 0%;
            top: 0px;
            margin-top: 20px;
            /* Solid / White - FEFEFE */
            cursor: pointer;
            background: #fefefe;
            border: 1px solid #f5f5f5;
            border-radius: 5px;
            display: flex;
            flex-direction: column;
            padding: 0.125rem 0;
            //margin: 1rem;
            .title {
              font-family: "Roboto";
              font-style: normal;
              font-weight: 700;
              font-size: 20px;
              line-height: 23px;

              color: #314150;
              display: flex;
              /* Inside auto layout */
              justify-content: space-between;
              flex: none;
              order: 0;
              flex-grow: 0;
              height: 40px;
              //margin: 1rem;
              padding: 0.5rem;
            }
            .content {
              height: 154px;
              left: 4.85%;
              right: 14.74%;
              top: 66px;

              font-family: "Roboto";
              font-style: normal;
              font-weight: 400;
              font-size: 16px;
              line-height: 170%;
              /* or 27px */
              max-height: 0;
              overflow: hidden;
              //transition: all 0.5s cubic-bezier(0, 1, 0, 1);
              color: #6f6d71;
              padding: 0.125rem;
            }
            .content-show {
              height: 100px;
              left: 4.85%;
              right: 14.74%;
              top: 66px;

              font-family: "Roboto";
              font-style: normal;
              font-weight: 400;
              font-size: 16px;
              line-height: 170%;
              /* or 27px */
              max-height: 9999;
              transition: all 0.5s cubic-bezier(0, 1, 0, 1);
              color: #6f6d71;
              padding: 0.125rem 0;
              margin: 1rem;
            }
          }
        }
      }
    }
  }
}

